			transition-delay: 1.625s;
			transition: .25s ease-in-out; }

		.cross_2 {
			width: 100%;
			transform: rotate(-45deg) translateY(49%) translateX(-1%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; } }

	.menu-icon {
		.menuIcon {
			.st0 {
				fill: #ffffff;
				stroke: #ffffff;
				stroke-width: 0; } }
		.top {
			transition-delay: 0.5s;
			transition: .25s ease-in-out; }
		.middle {
			transition-delay: 0.625s;
			transition: .35s ease-in-out; }
		.bottom {
			transition-delay: 0.750s;
			transition: .45s ease-in-out; }

		.leaves {
			transform: translateX(0);
			transition-delay: 0.350s;
			transition: .45s ease-in-out; }
		.word {
			.m {
				transform: translateY(0);
				transition-delay: 0s;
				transition: .45s ease-in-out; }
			.e {
				transform: translateY(0);
				transition-delay: 0.125s;
				transition: .55s ease-in-out; }
			.n {
				transform: translateY(0);
				transition-delay: 0.250s;
				transition: .65s ease-in-out; }
			.u {
				transform: translateY(0);
				transition-delay: 0.300s;
				transition: .75s ease-in-out; } }
		.cross_1 {
			width: 0%;
			transform: rotate(45deg) translateY(49%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; }

		.cross_2 {
			width: 0%;
			transform: rotate(-45deg) translateY(49%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; } } }
.language {
	position: relative;
	.lang_dropdown {
		position: absolute;
		right: -20px;
		top: 5px;
		max-height: 0;
		width: 46.33px;
		overflow: hidden;
		transition: .35s;
		opacity: 0;
		box-sizing: border-box; }
	.lang-dropdown--active {
		max-height: 60px;
		opacity: 1; }
	.lang--active {
		display: block;
		&:focus {
			outline: 1px rgba(255,255,255,.6) solid; } } }
.language {
	display: flex;
	align-items: center;
	margin-right: 33px;
	&-btn {
		position: relative;
		width: 30px;
		height: 30px;
		margin-right: 15px;
		display: block;
		border-radius: 50%;
		background-color: #689a23;
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			content: '';
			transform: translate(-50%,0);
			border: 4px solid transparent;
			border-top: 4px solid #fff;
			transition: all 0.4s; }
		&:hover {
			cursor: pointer;
			&::before {
				transform: rotate(-180deg);
				transform-origin: 28% 20%; } }
		&:focus {
			outline: 1px rgba(255,255,255,.6) solid; } }
	&:hover {
		.lang--active {
			margin-top: -38px;
			line-height: 20px;
			transition: all 0.4s; } }
	.lang__item {
		line-height: 20px;
		box-sizing: border-box;
		a {
			margin-left: 1px;
			position: relative;
			box-sizing: border-box;
			&:focus {
				outline: 1px rgba(255,255,255,.6) solid; }
			&::after {
				position: absolute;
				bottom: -2px;
				left: 0;
				content: '';
				display: block;
				height: 1px;
				width: 100%;
				background-color: #fff;
				transform: scale(0); }
			&:hover {
				&::after {
					transition: 0.4s;
					transform: scale(1); } } } }
	&__list {
		max-height: 20px;
		overflow: hidden;
		line-height: 1.5;
		transition: 0.6s;
		&-active {
			max-height: 44px;
			height: auto; } } }
/* ===========================Home_main_slaider========================*/
